ft:60%}.container_svg svg ellipse{stroke:cyan;stroke-width:2px;fill:white}.container_svg .texto-svg{position:absolute;top:25%;right:-140%}@keyframes aparicion{to{opacity:1}from{opacity:0}}@keyframes clickme{to{transform:rotate(0deg)}from{transform:rotate(-360deg)}}@keyframes clickMaquina{to{opacity:1;transform:rotate(0deg)}from{opacity:0;transform:rotate(-360deg)}}@media (max-width: 450px){.resul_user{top:40%;font-size:16px}.resul_maquina{top:40%;left:60%;font-size:16px}.container-opc .piedra,.container-opc .papel,.container-opc .tijera{width:70px;height:70px}.container-opc .piedra img,.container-opc .papel img,.container-opc .tijera img{width:30px;height:30px}.container-opc .piedra{left:-30px}.container-opc .papel{left:-30px}.container-opc .tijera{left:-30px;top:90%}.opc-maquina{width:90px;height:70px;margin-top:40px}.opc-maquina .maquina img{width:30px;height:30px}.container_svg{top:45%;left:0%}.container_svg svg ellipse{stroke:cyan;stroke-width:2px;fill:white}.container_svg .texto-svg{position:absolute;top:25%;right:-140%}.container_buttons{margin-left:30%}.container_buttons .button_play{width:100%;font-size:10px}.container_buttons .button_play-again{width:100%;font-size:10px}}.component_score{display:flex;justify-content:center;margin-top:50px}.container_score{color:cyan;bottom:0;display:flex;justify-content:space-between;left:40%;height:15%;position:absolute;right:0;top:10%;width:20%}.container_score .container_title{display:flex;padding:10px;flex-direction:column}.container_score .container_title strong{padding:5px;text-shadow:3px 3px 5px cyan}.container_score .score{border-radius:10%;color:cyan;height:50%;border:1px solid cyan;margin:20px 20px 5px 0;padding:5px;text-align:center;text-shadow:3px 3px 5px cyan;width:50%}.container_score .score p{margin:0}.container_score .score h1{margin:0}.pruebas_svg{width:100%;text-align:center}.pruebas_svg svg rect{fill:transparent;stroke:cyan;stroke-dashoffset:1000;width:100%;height:80%;stroke-dasharray:50;animation:dash 4s